<script>
import DataList from "@/components/datalist";

export default {
    name: "page-orders-addlist",
    components: { DataList },
    data() {
        return {
            loading: false,
            orderStatusList: [
                { value: 0, text: "" },
                { value: 1, text: "" },
                { value: 2, text: "" },
                { value: 3, text: "" },
                { value: 4, text: "" },
                { value: 5, text: "" },
                { value: 6, text: "" },
                { value: 7, text: "" },
                { value: 8, text: "" },
                { value: 9, text: "" },
                { value: 10, text: "" },
                { value: 11, text: "" },
                { value: 12, text: "" },
                { value: 13, text: "" },
                { value: 14, text: "" }
            ],
            filter: {
                orderNo: "",
                orderStatus: -1
            },
            layer: false,
            info: {},
            // orderStatusList: {},
            arr: [
                {
                    name: "",
                    xinhao: "",
                    guige: "",
                    num: "",
                    biaozhun: "",
                    lianjiek: "",
                    jiaodu: "",
                    zhongxinju: "",
                    xinshi: "",
                    length: "",
                    tuhao: "",
                    jinniuju: "",
                    jishuxinhao: ""
                },
                {
                    name: "",
                    xinhao: "",
                    guige: "",
                    num: "",
                    biaozhun: "",
                    lianjiek: "",
                    jiaodu: "",
                    zhongxinju: "",
                    xinshi: "",
                    length: "",
                    tuhao: "",
                    jinniuju: "",
                    jishuxinhao: ""
                }
            ]
        };
    },
    created() {
        // this.getOrderStatusList();
    },
    methods: {
        getOrderStatusList() {
            this.$http
                .get("/haolifa/order-product/order-status-list")
                .then(res => {
                    this.orderStatusList = res.map(item => {
                        return { value: item.code, text: item.desc };
                    });
                });
        },
        progress(item) {
            let id = "";
            this.$http
                .post("/haolifa/flowInstance/create", {
                    flowId: 1,
                    formId: item.id,
                    formType: 1,
                    formNo: item.orderNo,
                    summary: ""
                })
                .then(res => {
                    id = res.instanceId;
                    // this.loading = false;
                    // this.$toast(`,ID: ${res.instanceId}`);
                    this.$http
                        .post("/haolifa/order-product/updateStatus", {
                            orderNo: item.orderNo,
                            status: 1
                        })
                        .then(res => {
                            this.loading = false;
                            this.$toast(`,ID: ${id}`);
                        });
                });
        },
        // 
        approveProgress(item) {
            this.$router.push(`/order/approveProgress?formNo=${item.orderNo}`);
        },
        infoShow(item) {
            // this.$router.push(`/order/info?orderNo=${item.orderNo}`);
            this.layer = true;
            this.getInfo(item.orderNo);
            // this.getOrderStatusList();
        },
        getInfo(orderNo) {
            this.$http
                .get(`/haolifa/order-product/details?orderNo=${orderNo}`)
                .then(res => {
                    this.info = res;
                })
                .catch(e => {
                    this.$toast(e.msg || e.message);
                });
        },
        // getOrderStatusList() {
        //     this.$http
        //         .get("/haolifa/order-product/order-status-list")
        //         .then(res => {
        //             for (let i in res) {
        //                 this.orderStatusList[res[i].code] = res[i].desc;
        //             }
        //         });
        // },
        close() {
            this.layer = false;
        },
        remove(item) {
            this.$confirm({
                title: "",
                text: `<br>${item.orderNo}`,
                color: "red",
                btns: ["", ""],
                yes: () => {
                    this.$http
                        .delete(`/haolifa/order-product/delete/${item.id}`)
                        .then(res => {
                            this.$toast("");
                            this.$refs.list.update();
                        })
                        .catch(e => {
                            this.$toast(e.msg);
                        });
                }
            });
        }
    }
};
</script>

