<script>
import DataList from "@/components/datalist";

export default {
    name: "cjzr-scddlb",
    components: { DataList },
    data() {
        return {
            loading: false,
            orderStatusList: [
                { value: 5, text: "" },
                { value: 6, text: "" },
                { value: 7, text: "" },
                { value: 8, text: "" },
                { value: 9, text: "" }
            ],
            filter: {
                orderNo: "",
                orderStatus: 5
            },
            layer: false,
            info: {},
            arr: [
                {
                    name: "",
                    xinhao: "",
                    guige: "",
                    num: "",
                    biaozhun: "",
                    lianjiek: "",
                    jiaodu: "",
                    zhongxinju: "",
                    xinshi: "",
                    length: "",
                    tuhao: "",
                    jinniuju: "",
                    jishuxinhao: ""
                },
                {
                    name: "",
                    xinhao: "",
                    guige: "",
                    num: "",
                    biaozhun: "",
                    lianjiek: "",
                    jiaodu: "",
                    zhongxinju: "",
                    xinshi: "",
                    length: "",
                    tuhao: "",
                    jinniuju: "",
                    jishuxinhao: ""
                }
            ]
        };
    },
    methods: {
        inspectHistorys(item) {
            this.$router.push(`/jhgl-scddlb/inspect?orderNo=${item.orderNo}`);
        },
        info(item) {
            this.$router.push(`/jhgl-scddlb/info?orderNo=${item.orderNo}`);
        },
        getInfo(orderNo) {
            this.layer = true;
            this.$http
                .get(`/haolifa/order-product/details?orderNo=${orderNo}`)
                .then(res => {
                    this.info = res;
                })
                .catch(e => {
                    this.$toast(e.msg || e.message);
                });
        },
        getOrderStatusList() {
            this.$http
                .get("/haolifa/order-product/order-status-list")
                .then(res => {
                    for (let i in res) {
                        this.orderStatusList[res[i].code] = res[i].desc;
                    }
                });
        }
    }
};
</script>

